.wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.box {
  background: url(https://images.pexels.com/photos/1220757/pexels-photos-1220757.jpg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center center;
  height: 130px;
  width: 130px;
  box-shadow: 0 20px 5px 5px rgba(0, 0, 0, 0.1);
  animation: animate 5s ease-in-out infinite;
  transition: all 1s ease-in-out;
  margin-top: 2px;
  margin-left: 35rem;
}

@keyframes animate {
  0% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 60% 70% 40%/50% 60% 30% 60%;
  }
  100% {
    border-radius: 60% 40% 30% 70%/60% 30% 70% 40%;
  }
}

.introduction {
  margin-top: 10%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  /* align-items: center; */
  /* justify-content: center; */
}

.horizontal-line {
  width: 35%;
  margin: 20px auto;
}

.image-container {
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  height: 25vh;
  width: 70vw;
}

.img-thumbnail {
  background: url(https://images.pexels.com/photos/1220757/pexels-photos-1220757.jpg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1);
  -webkit-background-size: cover;
  background-size: cover;
  background-position: center center;
  height: 130px;
  width: 130px;
  box-shadow: 0 20px 5px 5px rgba(0, 0, 0, 0.1);
  animation: animate 5s ease-in-out infinite;
  transition: all 1s ease-in-out;
}

.intro-heading {
  font-family: "IBM Plex Sans", Courier, monospace;
  font-size: 3rem;
}

.text-intro {
  font-family: "IBM Plex Sans", Courier, monospace;
  font-size: 25px;
  margin-right: 25%;
  text-align: end;
  margin-top: 15px;
  display: flex;
  justify-content: center;
}

.nav-container {
  margin-top: 40px;
  margin-right: 15%;
  display: flex;
  text-align: left;
}
.list-items {
  list-style-type: none;
}
.list {
  padding-bottom: 25px;
  font-size: 18px;
}

.link-franskbarek {
  text-decoration: none;
  color: black;
  background-image: linear-gradient(transparent 0%, transparent calc(50% - 9px), rgb(255, 230, 0) calc(50% - 9px), rgba(5, 5, 5, 0.35) 100%);
  transition: all 120ms ease-in-out 0s;
  background-size: 100% 200%;
  background-position: 0px 0px;
  font-family: "IBM Plex Sans", Courier, monospace;
  margin-left: 10px;
}

.link-franskbarek:hover {
  text-decoration: none;
  color: black;
  background-image: linear-gradient(transparent 0%, transparent calc(50% - 9px), rgb(255, 230, 0) calc(50% - 9px), rgba(255, 238, 2, 0.965) 100%);
  transition: all 120ms ease-in-out 0s;
  background-size: 100% 100%;
  background-position: 0px 0px;
  font-family: "IBM Plex Sans", Courier, monospace;
  margin-left: 10px;
}

.social-container {
  display: flex;
  justify-content: end;
  margin-right: -400px;
}

.social-item {
  display: flex;
  justify-content: end;
  margin-right: 20px;
  text-decoration: none;
  color: #3c3c3c;
  font-size: 20px;
}

@media screen and (max-width: 1000px) {
  .introduction {
    margin-top: 25%;
    max-width: 100%;
  }
  .header-container {
    margin: 5%;
  }
  .image-container {
    width: 90%;
    margin-left: auto;
  }
  .img-thumbnail {
    margin: auto;
  }
  .horizontal-line {
    width: 90%;
  }
  .text-intro {
    margin-right: 22%;
    font-size: 110%;
  }
  .intro-heading {
    font-size: 190%;
    text-align: left;
  }
  .nav-container {
    margin-left: 8%;
  }
  .social-container {
    display: flex;
    justify-content: end;
    margin: auto;
  }
}
