.project-container {
  width: 80vw;
}

.project-headTitle {
  font-family: "Courier New", Courier, monospace;
  font-size: 2em;
  text-decoration: underline;
  margin-left: 100px;
  margin-top: 20px;
  margin-bottom: 40px;
}

.project {
  display: grid;
  grid-template-columns: auto auto;
  justify-content: center;
  gap: 10px;
}

.project-link {
  font-size: 20px;
}

@media screen and (max-width: 1024px) {
  .project {
    display: grid;
    grid-template-columns: auto;
    justify-content: center;
    margin: 0px 20px;
    gap: 10px;
  }
  .project-headTitle {
    font-size: 140%;
    margin-left: 6%;
  }
  .project-image {
    width: 100%;
  }
}
